import React, { Component } from 'react'
import injectSheet from 'react-jss'
import CableInfoSection from './cableInfoSection'
import { cables } from '../constants'

const styles = {
  wrapper: {
    background: `linear-gradient(-45deg, #2c5a63, #1c234d, #2c5a63, #1c234d)`,
    backgroundSize: '400% 400%',
    animation: '$gradient 15s ease infinite',
    paddingBottom: 100,
  },
  '@keyframes gradient': {
    '0%': {
      backgroundPosition: '0% 50%'
    },
    '50%': {
      backgroundPosition: '100% 50%'
    },
    '100%': {
      backgroundPosition: '0% 50%'
    }
  }
}

class CableInfoWrapper extends Component {
  render() {
    const { classes, refs } = this.props
    return (
      <div className={classes.wrapper}>
        {refs && cables.map((cable, idx) => {
          return <CableInfoSection scrollRef={refs[`cable${idx}`]} key={cable.sort} cable={cable}/>
        })
        }
      </div>
    )
  }
}
export default injectSheet(styles)(CableInfoWrapper)
