import React, { Component } from 'react'
import injectSheet from 'react-jss'
import { colors } from '../constants'
import { mediaWidth, gridColumnTemplate } from '../helpers'

const styles = {
  container: {
    width: '100%',
    display: 'grid',
    height: 'auto',
    ...gridColumnTemplate(),
    gridTemplateRows: '100px auto 10px auto',
    ...mediaWidth(600, {
      gridTemplateRows: '50px auto 10px auto',
    })
  },
  background: {
    gridColumn: '1 / -1',
    gridRow: '1 / -1',
    // backgroundColor: colors.black
  },
  textLeft: {
    gridColumn: '2 / -8',
    marginRight: 40, 
    ...mediaWidth(768,{
      gridColumn: '2 / -2',
    }),
  },
  textRight: {
    gridColumn: '8 / -2',
    marginLeft: 40,
    ...mediaWidth(768,{
      gridColumn: '2 / -2',
    }),
  },
  textContainer: {
    maxWidth: '100%',
    gridRow: '2 / 3',
    ...mediaWidth(768,{
      gridRow: '4 / -2',
    }),
    '& h2': {
      display: 'block',
      color: colors.white,
      fontSize: '1.8em',
      margin: '30px 0'
    },
    '& h2::after': {
      content: '""',
      display: 'block',
      position: 'absolute',
      width: 225,
      height: 5,
      background: colors.mainColor,
      marginTop: 15
    },
    '& p': {
      fontSize: '1.2em',
      color: colors.white
    }
  },
  imgLeft: {
    gridColumn: '2 / -8',
    ...mediaWidth(768,{
      gridColumn: '2 / -2',
    }),
  },
  imgRight: {
    gridColumn: '8 / -2',
    ...mediaWidth(768,{
      gridColumn: '2 / -2',
    }),
  },
  image: {
    position: 'relative',
    border: `10px solid ${colors.white}`,
    marginTop: 60,
    maxWidth: 'calc(100% - 20px)',
    gridRow: '2 / 3'
  }
}

class CableInfoSection extends Component {
  constructor (props) {
    super(props)
    this.state = {
      scrolled: 0,
    }
    this.myRef = React.createRef();
    this.myRef2 = React.createRef();
  }
  componentDidMount () {
    window.addEventListener('scroll', this.handleScroll.bind(this));
  }      
  componentWillUnmount () {
    window.removeEventListener('scroll', this.handleScroll);
  }
  handleScroll () {
    // this.props.scrollRef.current.offsetTop
    // window.pageYOffset
    // window.getComputedStyle(this.myRef).getPropertyValue("background")
    this.myRef.style.top = `${(window.pageYOffset - this.myRef.offsetTop) * 0.1}px`
    this.myRef2.style.top = `${(window.pageYOffset - this.myRef2.offsetTop) * 0.2}px`
  }
  render() {
    const { scrolled } = this.state
    const { classes, cable, scrollRef } = this.props
    return (
      <div ref={scrollRef} className={classes.container}>
        <div className={classes.background}></div>
        <div
          ref={el => this.myRef2 = el}
          className={`${classes.textContainer} ${cable.sort % 2 !== 0
            ? classes.textLeft : classes.textRight}`}
        >
          <h2>{cable.name + ' ' + scrolled}</h2>
          <p>{cable.info}</p>
        </div>
        <img
          ref={el => this.myRef = el}
          className={`${classes.image} ${cable.sort % 2 !== 0
            ? classes.imgRight : classes.imgLeft}`}
          src={cable.picture}
          alt={cable.name}
        />
      </div>
    )
  }
}
export default injectSheet(styles)(CableInfoSection)