import React, { Component } from 'react'
import injectSheet from 'react-jss'
import { colors } from '../constants'
import { mediaWidth, gridColumnTemplate } from '../helpers'
// import Logo from '../assets/svg/logo'

const styles = {
  root: {
    width: '100%',
    display: 'grid',
    height: 'auto',
    ...gridColumnTemplate(),
    gridTemplateRows: '100px auto auto',
    backgroundColor: colors.white,
    ...mediaWidth(600, {
      gridTemplateRows: '50px auto auto auto',
    })
  },
  logo: {
    gridColumn: '2 / -9',
    gridRow: '2 / 3',
    ...mediaWidth(600, {
      width: '100%',
      height: 'auto',
      gridColumn: '2 / -2',
    })
  },
  infoContainer: {
    gridColumn: '9 / -2',
    gridRow: '2 / 3',
    '& span': {
      display: 'block',
      textAlign: 'right'
    },
    ...mediaWidth(600, {
      gridColumn: '2 / -2',
      gridRow: '3 / 4',
      margin: '50px 0px 0px 0px',
      '& span': {
        display: 'block',
        textAlign: 'center'
      },
    })
  },
  copy:{
    margin: '100px 0px 20px 0px',
    textAlign: 'center',
    gridColumn: '2 / -2',
    gridRow: '3 / 4',
    ...mediaWidth(600, {
      gridColumn: '2 / -2',
      gridRow: '4 / 5',
      textAlign: 'center',
      margin: '20px 0px 20px 0px',
    })
  },
  logoSvg: {
    fill: colors.mainColor,
  }
}

class Footer extends Component {
  render() {
    const { classes } = this.props
    return (
      <div className={classes.root}>
        <img
          className={classes.logo}
          height='100%'
          src={require('../assets/img/logo5blue.png')}
          alt=''
        />
        <div
         className={classes.infoContainer}
        >
          <span>Bertram Audio</span>
          <span>Some address 123</span>
          <span>city 45 678</span>
          <span>Sweden</span>

         </div>
         <span className={classes.copy}>All content &copy; 2020 by Bertram Audio</span>
      </div>
    )
  }
}
export default injectSheet(styles)(Footer)
